import React from 'react'
import { Grid, Typography } from '@material-ui/core'
import TextFieldCurrency from '../../TextFieldCurrency/TextFieldCurrency'
import TextField from './../../TextField/CustomTextField'
// Style
import classes from '../style.module.scss'

const ACDFGrid = props => {
    const {
        labels,
        data,
        _data,
        breakdownName,
        tax,
        disabled,
        UpdateField,
        setValue,
        dispatch } = props
    return (
        <>
          <Grid container spacing={4} className={`${classes['margin-top']} ${classes['bg-gray']}`}>
            <Grid item xs={12} lg={12}>
              <Typography variant='h5'>{breakdownName} Breakdown (Adjustments)</Typography>
            </Grid>
            <Grid item xs={3}>
              <TextFieldCurrency label={'Credit Memo'} disabled={!!disabled} name='credit_memo' onChange={e => {
                  if (!!disabled) return false
                  const taxAmount = e.target.value * tax
                      dispatch(
                        UpdateField({
                          value: {
                            ..._data.adjustments,
                            credit_memo: e.target.value,
                            credit_memo_tax: taxAmount
                          },
                          field: 'adjustments'
                        })
                      )
                      setValue(Math.random())
                    }} value={_data.adjustments ? _data.adjustments.credit_memo : 0 } />
            </Grid>
            <Grid item xs={3}>
              <TextFieldCurrency label={'Credit Memo Tax'} disabled value={_data.adjustments ? _data.adjustments.credit_memo_tax : 0 } />
            </Grid>
            <Grid item xs={3}>
              <TextFieldCurrency label={'Deletion'} disabled={!!disabled} onChange={e => {
                  if (!!disabled) return false
                  const taxAmount = e.target.value * tax
                      dispatch(
                        UpdateField({
                          value: {
                            ..._data.adjustments,
                            deletion: e.target.value,
                            deletion_tax: taxAmount
                          },
                          field: 'adjustments'
                        })
                      )
                      setValue(Math.random())
                    }} value={_data.adjustments ? _data.adjustments.deletion : 0 } />
            </Grid>
            <Grid item xs={3}>
              <TextFieldCurrency label={'Deletion Tax'} disabled value={_data.adjustments ? _data.adjustments.deletion_tax : 0 } />
            </Grid>
          </Grid>
          <Grid container spacing={4} className={`${classes['no-margin']} ${classes['bg-gray']}`}>
            <Grid item xs={3}>
              <TextFieldCurrency label={'EWT'} disabled={!!disabled} onChange={e => {
                  if (!!disabled) return false
                  dispatch(
                        UpdateField({
                          value: {
                            ..._data.adjustments,
                            ewt: e.target.value,
                          },
                          field: 'adjustments'
                        })
                      )
                      setValue(Math.random())
                    }} value={_data.adjustments ? _data.adjustments.ewt : 0 } />
            </Grid>
            <Grid item xs={3} />
            <Grid item xs={3}>
              <TextFieldCurrency label={'Commission'} disabled={!!disabled} onChange={e => {
                  if (!!disabled) return false
                  const taxAmount = e.target.value * tax
                      dispatch(
                        UpdateField({
                          value: {
                            ..._data.adjustments,
                            commision: e.target.value,
                            commision_tax: taxAmount
                          },
                          field: 'adjustments'
                        })
                      )
                      setValue(Math.random())
                    }} value={_data.adjustments ? _data.adjustments.commision : 0 } />
            </Grid>
            <Grid item xs={3}>
              <TextFieldCurrency label={'Commission Tax'} disabled value={_data.adjustments ? _data.adjustments.commision_tax : 0 } />
            </Grid>
            <Grid item xs={12} sm={12} md={12} lg={12}>
              <TextField
                constainerStyle={{ width: 'unset', flex: 1 }}
                multiline
                rows={3}
                disabled={!!disabled}
                id='remarks'
                label='Remarks'
                value={_data.adjustments ? _data.adjustments.remarks : '' }
                onChange={ e => {
                  if (!!disabled) return false
                  dispatch(
                    UpdateField({
                      value: {
                        ..._data.adjustments,
                        remarks: e.target.value,
                      },
                      field: 'adjustments'
                    })
                  )
                }}
              />
            </Grid>
          </Grid>
          <Grid
            container
            direction='row'
            justify='space-between'
            className={`${classes['no-margin']} ${classes['bg-gray']}`}
            spacing={4}
          >
            <Grid item xs={12}>
              <Typography variant='h5'>Sub Total (Adjustments)</Typography>
            </Grid>
            <Grid item xs={4}>
              <TextFieldCurrency label='Sub Total Taxable Amount' disabled value={_data.total_fees ? _data.total_fees.sub_total : 0} />
            </Grid>
            <Grid item xs={4} alignContent='flex-end'>
              <TextFieldCurrency label='Sub Total Tax Amount' disabled value={_data.total_fees ? _data.total_fees.sub_total_tax : 0} />
            </Grid>
          </Grid>
        </>
    )
}

export default ACDFGrid