import React from 'react'
import { Grid, Typography } from '@material-ui/core'
import TextFieldCurrency from '../TextFieldCurrency/TextFieldCurrency'
import TextField from './../TextField/CustomTextField'
// Store
import { useSelector, useDispatch } from 'react-redux'
import { UpdateField } from 'Pages/Protected/BillingAndCollection/Store/actions'

// Style
import classes from './style.module.scss'

// GRID
import CDFGrid from './grids/CDFGrid'
import ACDFGrid from './grids/ACDFGrid'

const GroupOfCurrency = ({ labels, data = {}, disabled = false, breakdownName = "Collection", isAdvance = false, taxInfo, adjustments }) => {
  const dispatch = useDispatch()
  const _data = useSelector(state => state.CollectionForm)
  const [tax, setTax] = React.useState(0)
  const [value, setValue] = React.useState('')
  

  React.useEffect( () => {
    const tax = taxInfo && taxInfo.tax_value ? taxInfo.tax_value.value : 0
    setTax(tax)
    setValue(Math.random())
  }, [taxInfo])

  React.useEffect( () => {
    const creditMemo = _data.adjustments && _data.adjustments.credit_memo ? _data.adjustments.credit_memo : 0
    const ewt = _data.adjustments && _data.adjustments.ewt ? _data.adjustments.ewt : 0
    const deletion = _data.adjustments && _data.adjustments.deletion ? _data.adjustments.deletion : 0
    const commision = _data.adjustments && _data.adjustments.commision ? _data.adjustments.commision : 0

    const creditMemoTax = _data.adjustments && _data.adjustments.credit_memo_tax ? _data.adjustments.credit_memo_tax : 0
    const deletionTax = _data.adjustments && _data.adjustments.deletion_tax ? _data.adjustments.deletion_tax : 0
    const commisionTax = _data.adjustments && _data.adjustments.commision_tax ? _data.adjustments.commision_tax : 0
    // const ewtTax = _data.adjustments && _data.adjustments.ewt_tax ? _data.adjustments.ewt_tax : 0
    dispatch(
      UpdateField({
        value: {
          ..._data.total_fees,
          sub_total: creditMemo + ewt + deletion + commision,
          sub_total_tax: creditMemoTax + deletionTax + commisionTax,
          grand_total: (creditMemo + creditMemoTax) + ewt + (deletion + deletionTax) + (commision + commisionTax)
        },
        field: 'total_fees'
      })
    )
  }, [value])

  const componentProps = {
    labels,
    data,
    _data,
    breakdownName,
    tax,
    disabled,
    UpdateField,
    setValue,
    dispatch
  }
  
  return (
    <Grid container className={classes.root}>
      {!isAdvance && <CDFGrid {...componentProps} />}

      {isAdvance && <ACDFGrid {...componentProps} />}
    </Grid>
  )
}

export default GroupOfCurrency
