import React from 'react'
import { Grid, Typography, IconButton } from '@material-ui/core'
import RemoveCircleRoundedIcon from '@material-ui/icons/RemoveCircleRounded'
import clsx from 'clsx'

import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos'
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos'

import moment from 'moment'

import { makeStyles } from '@material-ui/core/styles'
import { useSelector, useDispatch } from 'react-redux'

// Style
import classes from './FormSlider.module.scss'

const numeral = require('numeral')

const FormSlider = props => {
  const _data = useSelector(state => state.CollectionForm)
  const defaultFunction = function () {}
  const {
    details = [],
    data = {},
    OnChanges = defaultFunction,
    OnRemove = defaultFunction,
    showForCollection = false,
    showSummary = false,
    isAdvance = false,
    disabled = false
  } = props

  const [slider, setSlider] = React.useState({
    count: 0,
    cut: 3,
    data: '',
    member: '',
    index: 0,
    selected: showSummary ? -1 : 0
  })

  React.useEffect(() => {
    setSlider(i => ({ ...i, count: details.length }), OnChanges(slider))
  }, [details, slider.index, slider.selected])

  React.useEffect(() => {
    OnChanges({ ...slider, member: details[0]?.soa_number })
  }, [details])

  return (
    <Grid className={classes.root} container direction='row'>
      {showSummary && (
        <Grid item xs={2}>
          <Grid className={classes['summary-container']} container direction='row'>
            <Grid
              className={`${classes['item-container']} ${classes['summary-wrapper']} ${
                slider.selected === -1 ? classes.active : ''
              }`}
              item
              onClick={e => {
                setSlider(p => ({ ...p, data: null, selected: -1 }))
              }}
            >
              <Grid container direction='column'>
                <Grid item xs>
                  <Grid container direction='row'>
                    <Typography
                      className={`${classes.title} ${slider.selected === -1 ? classes.active : ''}`}
                      variant='h6'
                    >
                      SUMMARY
                    </Typography>
                  </Grid>
                </Grid>
                <Grid item xs>
                  <Typography className={`${classes.details} ${classes.highlight}`}>Total Taxable Amount</Typography>
                </Grid>
                <Grid item xs>
                  <Typography className={`${classes.details} ${classes.highlight}`}>
                    PHP {numeral(isAdvance ? _data.total_fees ? _data.total_fees.sub_total : 0 : data.sub_total || 0).format('0,0.00')}
                  </Typography>
                </Grid>
              </Grid>
            </Grid>
          </Grid>
        </Grid>
      )}

      {slider.index !== 0 && (
        <Grid item>
          <Grid className={classes['action-container']} container direction='column' justify='flex-start'>
            <Grid item>
              <IconButton
                onClick={() => {
                  setSlider(i => ({ ...i, index: i.index - i.cut, selected: 0 }))
                }}
              >
                <ArrowBackIosIcon fontSize='large' />
              </IconButton>
            </Grid>
          </Grid>
        </Grid>
      )}

      <Grid item xs>
        {/* SOA Container */}
        <Grid container direction='row' spacing='5'>
          {/* SOA Item */}
          {details &&
            details.slice(slider.index, slider.index + slider.cut).map((soa, i) => (
              <Grid
                className={`${classes['item-container']} ${i === slider.selected ? classes.active : ''}`}
                item
                xs={4}
                onClick={e => {
                  setSlider(p => ({ ...p, data: soa.soa_number, member: soa.soa_number, selected: i }))
                }}
              >
                <Grid container direction='column'>
                  <Grid item xs>
                    <Grid container direction='row'>
                      <Grid item>
                        <Typography
                          className={`${classes.title} ${i === slider.selected ? classes.active : ''}`}
                          variant='h6'
                        >
                          {soa.soa_number}
                        </Typography>
                      </Grid>
                      {!disabled && (
                        <Grid className={classes['action-remove-container']} item>
                          <IconButton className={classes['action-remove']}>
                            <RemoveCircleRoundedIcon
                              onClick={e => {
                                e.stopPropagation()
                                OnRemove(soa.soa_number)
                              }}
                            />
                          </IconButton>
                        </Grid>
                      )}
                    </Grid>
                  </Grid>
                  {showForCollection && (
                    <Grid item xs>
                      <Typography className={`${classes.details} ${classes.highlight} ${classes.bold}`}>
                        For Collection: PHP {numeral(soa.for_collection || 0).format('0,0.00')}
                      </Typography>
                    </Grid>
                  )}
                  <Grid item xs>
                    <Typography className={classes.details}>
                      Balance: PHP {numeral(soa.for_collection || 0).format('0,0.00')}
                    </Typography>
                  </Grid>
                  <Grid item xs>
                    <Typography className={classes.details}>
                      SOA Amount: PHP {numeral(soa.summary?.sub_total || 0).format('0,0.00')}
                    </Typography>
                  </Grid>
                  <Grid item xs>
                    <Typography className={classes.details}>
                      Period Covered: {moment(soa.period?.start).format('ll')} - {moment(soa.period?.end).format('ll')}
                    </Typography>
                  </Grid>
                  <Grid item xs>
                    <Typography className={classes.details}>
                      Due: {moment(soa.due_date || null).format('ll')}
                    </Typography>
                  </Grid>
                </Grid>
              </Grid>
            ))}
        </Grid>
      </Grid>
      {slider.index + slider.cut < slider.count && (
        <Grid item>
          <Grid className={classes['action-container']} container direction='column' justify='flex-start'>
            <IconButton
              onClick={() => {
                setSlider(i => ({ ...i, index: i.index + i.cut, selected: 0 }))
              }}
            >
              <ArrowForwardIosIcon fontSize='large' />
            </IconButton>
          </Grid>
        </Grid>
      )}
    </Grid>
  )
}

export default FormSlider
