import React from 'react'
import { Link, useHistory } from 'react-router-dom'
import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography'
import { EditTwoTone } from '@material-ui/icons'
import { Button, TextField } from '@material-ui/core'
import TableCell from '@material-ui/core/TableCell'
import TableBody from '@material-ui/core/TableBody'
import TableRow from '@material-ui/core/TableRow'
import _ from 'lodash'

import RemoveCircleIcon from '@material-ui/icons/RemoveCircle';
import CheckCircleIcon from '@material-ui/icons/CheckCircle';
import CancelIcon from '@material-ui/icons/Cancel';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faEdit } from '@fortawesome/free-solid-svg-icons'

// Components
import PageFooter from './../../../Components/PageFooter/PageFooter'
import SearchTextComponent from 'SharedComponents/SearchTextComponent/SearchTextComponent'
import Select from '../../../Components/FormComponents/Select/Select';
import CustomEnhancedTable from '../../../Components/CustomEnhancedTable/CustomEnhancedTable';
import CircularLoading from 'SharedComponents/CircularLoading/CircularLoading'

// Services
import ScheduleLetterServices from 'Services/Api/CollectionMaintenance/ScheduleLetterServices';
import BillingAnalystServices from 'Services/Api/BillingAnalystServices';

// Hooks
import useForm from 'Hooks/useForm'

// Utilities
import pick from 'lodash/pick'
import upperFirst from 'lodash/upperFirst'
import moment from 'moment'
import { CurrencyFormatter } from 'Utilities/TextFormatter'

// MuiStyles
import { withStyles } from '@material-ui/core/styles'

// Styles
import classes from './ScheduleLetters.module.scss'

// config
import { accountsConfig } from './config/generate-config'
import rows from 'Pages/Protected/Partner/ProfileMaintainer/HospitalProfile/Pages/StandardRates/RawData'

const StyledButton = withStyles({
  root: {
    background: '#3ab77d',
    '&:hover': {
      background: '#0b4930'
    },
    borderRadius: 3,
    boxShadow: 'none',
    border: 0,
    color: 'white',
    height: 45,
    width: '130px',
    padding: '0 30px',
    marginBottom: '.5rem'
  },
  label: {
    textTransform: 'capitalize'
  }
})(Button)

const StyledTableCell = withStyles(theme => ({
  head: {
    backgroundColor: theme.palette.common.grey,
    color: theme.palette.common.white
  },
  body: {
    fontSize: 14
  }
}))(TableCell)

const StyledTableRow = withStyles(theme => ({
  root: {
    '&:nth-of-type(odd)': {
      backgroundColor: theme.palette.action.hover
    }
  }
}))(TableRow)

export default function ScheduleLetters () {
  const history = useHistory()

  const initialValue = {
    header: HEADERS,
    accountData: [],
    body: [],
    count: 0,
    limit: 10,
    order: true,
    orderBy: '_id',
    filters: null,
    page: 1,
    sort: '',
    search: ''
  }

  const [list, setList] = React.useState(initialValue)
  const [isFetching, setIsFetchingState] = React.useState(false)
  const [params, setParams] = React.useState({})

  const [clientContractList, setClientContractList] = React.useState([])
  const [acccountNumberList, setAcccountNumberList] = React.useState([])
  const [acccountNameList, setAcccountNameList] = React.useState([])

  const form = useForm({
    accountNumber: '',
    daysPriorDueDate: ''
  });

  const createScheduleLetter = () => {
    ScheduleLetterServices.store(form.values).then(() => {
      form.updateValue("accountNumber", '')
      form.updateValue("daysPriorDueDate", '')
      fetchData(params);
    })
  }

  const fetchData = (params) => {
    setIsFetchingState(true)

    const _params = {
      limit: list.limit,
      page: list.page,
      sort: `${list.orderBy}|${list.order ? 'asc' : 'desc'}`,
      filters: list.filters,
      ...params
    }
    setParams(_params)

    ScheduleLetterServices.index(_params).then(({ data: res }) => {
      const { results } = res.data
      const orderByValue = _params.orderBy ? _params.orderBy : list.orderBy
      const orderValue = typeof _params.order === "boolean" ? _params.order : list.order
      setList(i => ({ ...i, body: results, sort: _params.sort, orderBy: orderByValue, order: orderValue }))
      setIsFetchingState(false)
    })
  }

  
  React.useEffect(() => {
    fetchData();

    BillingAnalystServices.getAllClientContract().then(values => {
      const resultA = values.data.data.map(item => { return {
        value: item.client.account_number, 
        text: item.client.account_number + " - " + item.client.account_name,
        accountNumber: item.client.account_number,
        accountName: item.client.account_name
      }})
      setClientContractList(resultA);

      const resultB = values.data.data.map(item => { return {
        value: item.client.account_number, 
        text: item.client.account_number,
      }})
      setAcccountNumberList(resultB);  
      
      const resultC = values.data.data.map(item => { return {
        value: item.client.account_number, 
        text: item.client.account_name,
      }})
      setAcccountNameList(resultC);    
    })
  }, [])

  const editSchedule = (row) => {
    row.edit = true;
    row.oldValue = {
      days_prior_due_date: row.days_prior_due_date,
      account_number: row.contract[0].client.account_number,
      account_name: row.contract[0].client.account_name
    }
    setList(prevState => ({ ...prevState, body: list.body }))
  } 

  const cancelEditSchedule = (row) => {
    row.days_prior_due_date = row.oldValue.days_prior_due_date
    row.contract[0].client.account_number = row.oldValue.account_number
    row.contract[0].client.account_name = row.oldValue.account_name
    row.edit = false;
    setList(prevState => ({ ...prevState, body: list.body }))
  }

  const saveEditSchedule = (row) => {
    
    const params = {
      id: row._id,
      accountNumber: row.contract[0].client.account_number,
      daysPriorDueDate: row.days_prior_due_date
    }

    ScheduleLetterServices.update(params).then(() => {
      row.edit = false;
      setList(prevState => ({ ...prevState, body: list.body }))
    })
  } 

  const removeSchedule = (row) => {
    ScheduleLetterServices.delete(row._id).then(() => {
      fetchData()
    })
  }

  const daysPriorOnChange = (event, row) => {
    event.persist();
    row.days_prior_due_date = event.target.value
    setList(prevState => ({ ...prevState, body: list.body }))
  }

  const accountOnChange = (event, row) => {
    event.persist();
    const accountName = clientContractList.find(item => item.accountNumber === event.target.value).accountName
    row.contract[0].client.account_name = accountName
    row.contract[0].client.account_number = event.target.value
    setList(prevState => ({ ...prevState, body: list.body }))
  }

  const mainSearchChange = _.debounce(e => {
    const params = {
      search: e.target.value,
      accessors: accessors
    }
    fetchData(params)
  }, 300)

  return (
    <>
      <Grid item xs={11} style={{ marginTop: '1rem' }}>
        <Grid container>
          <Grid item xs={2}>
            <Typography style={{padding: "15px 0 0 15px", fontWeight: "bold" }}>
              Account No*
            </Typography>
          </Grid>
          <Grid item xs={3}>
            <Select 
              defaultItemText="Select Account Number"
              name="accountNumber"
              value={form.values.accountNumber}
              onChange={form.onChange}
              data={acccountNumberList} 
              variant="outlined"
            />
          </Grid>

          <Grid item xs={2}>
          </Grid>

          <Grid item xs={2}>
            <Typography align="right" style={{padding: "15px 15px 0 15px", fontWeight: "bold" }}>
              Account Name*
            </Typography>
          </Grid>
          <Grid item xs={3}>
            <Select 
              defaultItemText="Select Account Name"
              name="accountNumber"
              value={form.values.accountNumber}
              onChange={form.onChange}
              data={acccountNameList} 
              variant="outlined"
            />
          </Grid>
        </Grid>
      </Grid>
      
      <Grid item xs={11} style={{ marginTop: '1rem' }}>
        <Grid container spacing={2} direction={'row'}>
          <Grid item xs={2}>
            <Typography style={{padding: "15px 0 0 15px", fontWeight: "bold" }}>
              Schedule
            </Typography>
          </Grid>
          <Grid item xs={3}>
            <TextField
              fullWidth 
              id="schedule"
              type="number"
              variant='outlined'
              size='small'
              name="daysPriorDueDate"
              value={form.values.daysPriorDueDate}
              onChange={form.onChange}
              InputLabelProps={{
                shrink: true,
              }}
              InputProps={{ inputProps: { min: 1 } }}
            />
          </Grid>
          <Grid item xs={3}>
            <Typography style={{padding: "15px 0 0 15px", fontWeight: "bold" }}>
              days prior SOA due date
            </Typography>
          </Grid>
          <Grid item xs={2}>
            <StyledButton onClick={() => createScheduleLetter()} >Add</StyledButton>
          </Grid>
        </Grid>
      </Grid>

      <Grid item xs={10}>
        <Grid container className={classes.search}>
          <Grid item xs>
            <SearchTextComponent
              placeholder='Search from the list of letter schedules'
              onChange={e => {
                e.persist()
                mainSearchChange(e)
              }}
            />
          </Grid>
        </Grid>
      </Grid>

      <Grid item xs={10}>
        <Grid container>
          <Grid item xs>
            <CustomEnhancedTable
              tableHeaders={HEADERS}
              search
              sort
              fetchData={fetchData}
              changeView={fetchData}
              params={params}
              pagination={false}
            >
              <TableBody>
                {isFetching && (
                  <StyledTableRow>
                    <StyledTableCell component='th' colspan={10}>
                      <CircularLoading style={{ marginLeft: '-200px' }}/>
                    </StyledTableCell>
                  </StyledTableRow>
                )}
                {!isFetching && list && list.body.map((row, contentsIndex) => (
                  <StyledTableRow key={contentsIndex}>
                    <StyledTableCell component='th'>
                      <Typography variant='caption' align='left'>
                        {row.edit ?
                          <Select 
                            defaultItemText="Select Account Number"
                            disableDefaultItemText={true}
                            name="accountNumber"
                            value={row.contract[0].client.account_number}
                            onChange={(event) => accountOnChange(event, row)}
                            data={acccountNumberList} 
                            variant="standard"
                            />
                          : row.contract[0].client.account_number
                        }
                      </Typography>
                    </StyledTableCell>
                    <StyledTableCell component='th'>
                      <Typography variant='caption' align='left'>
                        {row.edit ?
                          <Select 
                            defaultItemText="Select Account Name"
                            disableDefaultItemText={true}
                            name="accountNumber"
                            value={ row.contract[0].client.account_number }
                            onChange={(event) => accountOnChange(event, row)}
                            data={acccountNameList} 
                            variant="standard"
                            />
                          : row.contract[0].client.account_name
                        }
                      </Typography>
                    </StyledTableCell>
                    <StyledTableCell component='th'>
                      <Typography variant='caption' align='left'>
                        {row.edit ?
                            <TextField
                              fullWidth 
                              id="schedule"
                              type="number"
                              name="daysPriorDueDate"
                              value={row.days_prior_due_date}
                              onChange={(event) => daysPriorOnChange(event, row)}
                              InputLabelProps={{
                                shrink: true,
                              }}
                              InputProps={{ inputProps: { min: 1 } }}
                              variant="standard"
                            />
                          : row.days_prior_due_date
                        }
                      </Typography>
                    </StyledTableCell>
                    <StyledTableCell component='th'>
                      <Typography variant='caption' align='right' style={{cursor: "pointer"}}>
                      { !row.edit ? 
                        <React.Fragment>
                          <FontAwesomeIcon style={{color: '#40b981', fontSize: '20px', marginBottom: '5px'}} icon={faEdit} onClick={() => { editSchedule(row) }} /> &nbsp;&nbsp;
                          {/* <EditTwoTone style={{color: "#40b981"}} onClick={() => editSchedule(row)} /> &nbsp;&nbsp; */}
                          <RemoveCircleIcon style={{color: "#fd6663"}} onClick={() => removeSchedule(row)} />
                        </React.Fragment>
                      : 
                        <React.Fragment>
                          <CheckCircleIcon style={{color: "#40b981"}} onClick={() => saveEditSchedule(row)} /> &nbsp;&nbsp;
                          <CancelIcon style={{color: "#fd6663"}}  onClick={() => cancelEditSchedule(row)}/>
                        </React.Fragment>
                      }
                      </Typography>
                    </StyledTableCell>
                  </StyledTableRow>
                ))}
              </TableBody>

            </CustomEnhancedTable>
          </Grid>
        </Grid>
      </Grid>

      <Grid item xs={12}>
        <PageFooter isControl handleOnCloseClicked={() => history.push('/')} />
      </Grid>
    </>
  )
}

const HEADERS = [
  {
    id: 'as-account-noe',
    numeric: false,
    disablePadding: false,
    label: 'Account No',
    search: true,
    sort: true,
    accessor: 'contract.client.account_number'
  },
  {
    id: 'as-account-name',
    numeric: false,
    disablePadding: false,
    label: 'Account Name',
    search: true,
    sort: true,
    accessor: 'contract.client.account_name'
  },
  {
    id: 'as-schedule',
    numeric: false,
    disablePadding: false,
    label: 'Schedule (no. of days prior due date)',
    search: true,
    sort: true,
    accessor: 'days_prior_due_date|isNumber'
  },
  {
    id: 'actions',
    numeric: false,
    disablePadding: false,
    label: ''
  }
]

const accessors = ['contract.client.account_number', 'contract.client.account_name', 'days_prior_due_date|isNumber']
