import React from 'react'

import Typography from '@material-ui/core/Typography'
import DoneIcon from '@material-ui/icons/Done'
import Grid from '@material-ui/core/Grid'

import FieldType from 'Constant/TermsAndConditions'

import { CurrencyFormatter } from 'Utilities/TextFormatter'

// Styles
import classes from './BillingTermsAndConditionsTab.module.scss'

export default function BillingTermsAndConditionsTab (props) {
  const { contract } = props
  const sectionToDisplay = ['Premium Refund', 'Refund Computation', 'Experience Refund', 'Escalation Clause']

  return (
    <React.Fragment>
      { contract && (
        <div className={classes.root}>
          { contract.terms_and_conditions &&
            contract.terms_and_conditions.map((item, indexSection) => (
              <div>
                <br />
                {item.field_section.toUpperCase() === 'EXPERIENCE REFUND' ? (
                  <Grid container spacing={2}>
                    <Grid item xs={12}>
                      <Typography id={`field-section-${indexSection}`} color='textSecondary' variant='caption'>
                        {item.field_section.toUpperCase()}
                      </Typography>
                      { item.field_values ?
                        item.field_values.map((v, indexItem) => (
                          <div>
                            <Typography
                              id={`field-name-${indexSection}-${indexItem}`}
                              style={v.name === 'Condition for Experience Refund' ? {} : { marginLeft:'10px' }}
                              color='textSecondary'
                              variant='caption'
                            >
                              {v.name}
                            </Typography>
                            { v.name === 'Condition for Experience Refund' ? (
                              <div>
                                <Typography
                                  id={`field-value-${indexSection}-${indexItem}`}
                                  style={{ fontWeight: 'bold' }}
                                  variant='caption'
                                >
                                  
                                  {v.values && v.values.map((val, index) => (
                                    <span>
                                      <DoneIcon style={{ fontSize: 'smaller' }} />&nbsp;
                                      {val}
                                    </span>
                                  ))}
                                </Typography>
                              </div>
                            ) : (
                            <Typography
                              id={`field-value-${indexSection}-${indexItem}`}
                              style={{ fontWeight: 'bold', marginLeft:'30px' }}
                              variant='caption'
                            >
                              { v.values && v.values.map((val, index) => 
                                v.type === FieldType.PERCENTAGE ?  val + "%" : val )
                              }
                            </Typography>
                            )}
                          </div>
                        )) : null 
                      }
                    </Grid>
                  </Grid>
                ) : (
                  <Grid container spacing={1}>
                    { item.field_values ?
                      item.field_values.map((v, indexItem) => (
                        sectionToDisplay.includes(item.field_section) ? (
                          <Grid item xs={12} md={6}>
                            <Typography id={`field-section-${indexSection}-${indexItem}`} color='textSecondary' variant='caption'>
                              {indexItem === 0 ? item.field_section.toUpperCase() : indexItem === 1 ? <br/> : '' }
                            </Typography>
                            <div>
                              <Typography
                                id={`field-name-${indexSection}-${indexItem}`}
                                color='textSecondary'
                                variant='caption'
                              >
                                {v.name}
                              </Typography>
                              <br />
                              { v.type === FieldType.PERCENTAGE ? (
                                <div>
                                  { v.name === 'Loss Ratio Range' && v.values ? (
                                    <div>
                                      <Typography
                                        id={`field-name-${indexSection}-${indexItem}-from`}
                                        style={{ marginRight:'30px' }}
                                        color='textSecondary'
                                        variant='caption'
                                      >
                                        From
                                      </Typography>
                                      <Typography
                                        id={`field-value-${indexSection}-${indexItem}-from`}
                                        style={{ fontWeight: 'bold', marginRight:'20px' }}
                                        variant='caption'
                                      >
                                        {v.values[0] + " %"}
                                      </Typography>
                                      <Typography
                                        id={`field-name-${indexSection}-${indexItem}-to`}
                                        style={{ marginRight:'30px' }}
                                        color='textSecondary'
                                        variant='caption'
                                      >
                                        To
                                      </Typography>
                                      <Typography
                                        id={`field-value-${indexSection}-${indexItem}-to`}
                                        style={{ fontWeight: 'bold', marginRight:'20px' }}
                                        variant='caption'
                                      >
                                        {v.values[1] + " %"}
                                      </Typography>
                                    </div>
                                  ): 
                                    <Typography
                                      id={`field-value-${indexSection}-${indexItem}`}
                                      style={{ fontWeight: 'bold' }}
                                      variant='caption'
                                    >
                                      {v.values && v.values.map((val, index) => val + '%')}
                                    </Typography>
                                  }
                                </div>
                              ) : v.type === FieldType.NUMBER ? (
                                <div>
                                  <Typography
                                    id={`field-value-${indexSection}-${indexItem}`}
                                    style={{ fontWeight: 'bold' }}
                                    variant='caption'
                                  >
                                    {v.values && v.values.map((val, index) => val)}
                                  </Typography>
                                </div>
                              ) : (
                                <div>
                                  <Typography
                                    id={`field-value-${indexSection}-${indexItem}`}
                                    style={{ fontWeight: 'bold' }}
                                    variant='caption'
                                  >
                                    
                                    {v.values && v.values.map((val, index) => (
                                      <span>
                                        <DoneIcon style={{ fontSize: 'smaller' }} />&nbsp;
                                        {val}
                                      </span>
                                    ))}
                                  </Typography>
                                </div>
                              )}
                            </div>
                          </Grid>
                        ) : null 
                      )) : null 
                    }
                  </Grid>
                )}
              </div>
            ))
          }
        </div>
      )}
    </React.Fragment>
  )
}
